<template>
  <div class="all">
    <div class="header">
      <div class="back" @click="back">
        <svg
          t="1665994572394"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="3557"
          width="200"
          height="200"
        >
          <path
            d="M710.4 838.4 358.4 492.8c-12.8-12.8-32-12.8-44.8 0l0 0c-12.8 12.8-12.8 32 0 44.8l352 352c12.8 12.8 32 12.8 44.8 0l0 0C723.2 876.8 723.2 851.2 710.4 838.4z"
            p-id="3558"
            fill="#ffffff"
          ></path>
          <path
            d="M358.4 531.2l352-352c12.8-12.8 12.8-32 0-44.8l0 0c-12.8-12.8-32-12.8-44.8 0L313.6 486.4c-12.8 12.8-12.8 32 0 44.8l0 0C326.4 544 345.6 544 358.4 531.2z"
            p-id="3559"
            fill="#ffffff"
          ></path>
        </svg>
      </div>
      <div class="title">发布历史</div>
    </div>
    <div class="selectlists">
      <div class="selectlist" v-for="(item, index) in history" :key="index">
      <div class="listbox" @click="xq(item._id)">
        <div class="nr" v-if="item.imageSrc.length === 1">
          <div class="listtitle">{{ item.title }}</div>
          <div class="listimg">
            <img :src="item.imageSrc[0]" alt="" />
          </div>
        </div>
        <div class="nrs" v-else-if="item.imageSrc.length > 1">
          <div class="listtitle">{{ item.title }}</div>
          <div class="listimgs">
            <div v-for="(i, index) in item.imageSrc" :key="index">
              <img :src="i" alt="" />
            </div>
          </div>
        </div>
        <div class="nrss" v-else>
          <div class="listtitle">{{ item.title }}</div>
        </div>
        <div class="bigfoot">
          <div class="listfoot">
            <div>{{ item.author }}</div>
            <div>{{ item.comment }}评论</div>
            <div>{{ new Date(item.time).toLocaleDateString() }}</div>
          </div>
          <div style="margin-right: 10px" @click="del(item._id)">X</div>
        </div>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { Toast } from 'vant';
export default {
  data() {
    return {
      uid: "",
      history: [],
    };
  },
  methods: {
    back() {
      this.$router.push("/my");
    },
    xq(id) {
      // console.log(id);
      this.$router.push({
        name: "lcontent",
        params: {
          id: id,
        },
      });
    },
    refash() {
      this.uid = localStorage.getItem("uid");
      axios({
        method: "get",
        url: `https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_user_article_list?uid=${this.uid}&skip&limit`,
      }).then((res) => {
        // console.log(res.data.data);
        this.history = res.data.data;
      });
    },
    del(id) {
      event.stopPropagation(),
        axios({
          method: "post",
          url: "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/del_user_article",
          data: {
            _id: id,
            uid: localStorage.getItem("uid"),
          },
        }).then((res) => {
          // console.log(res);
          this.refash()
        });
        Toast('删除成功')
    },
  },
  created() {
    this.refash()
  },
};
</script>

<style scoped>
