  background-color: rgb(7,193,96);
    height: 45px;
    line-height: 45px;
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
}
.back {
  /* border: 1px solid red; */
  height: 45px;
  width: 30px;
  margin-left: 13px;
  line-height: 53px;
}
.icon {
  height: 20px;
  width: 20px;
}
.title {
  /* border: 1px solid red; */
  color: white;
  width: 290px;
  text-align: center;
}
svg {
  width: 28px;
  height: 28px;
}
.selectlist {
  border-bottom: 1px solid #efefef;
  margin: 10px 0;
  padding-left: 10px;
}
.selectlists{
  /* border: 1px solid red; */
  margin-top: 45px;
}
.listtitle {
  /* border: 1px solid red; */
  margin-left: 3px;
  font-weight: bold;
  font-size: 16px;
  padding: 0 0 20px 0;
}
.listimg img {
  width: 150px;
  margin-right: 20px;
}
.listimgs {
  /* border: 1px solid red; */
  display: flex;
  justify-content: left;
}
.listbox {
  border-bottom: 1px solid #efefef;
  padding-left: 10px;
}
.listimgs img {
  width: 111px;
  margin: 2px 5px;
}
.listfoot {
  /* border: 1px solid blue; */
  display: flex;
  justify-content: left;
  align-items: center;
  font-size: 14px;
  color: #666;
}
.listfoot div {
  margin-left: 5px;
  /* border: 1px solid red; */
}
.nr {
  display: flex;
  justify-content: space-between;
  margin-right: 10px;
}

.bigfoot {
  display: flex;
  /* border: 1px solid red; */
  justify-content: space-between;
  margin: 5px 0;
  /* align-items: center; */
}
